<!--  -->
<template>
<div class='home'>
    <div class='search-box'>
        <div class='search' @click='searchName'>
            <img src="../../../static/image/search.png" alt="" class='search-icon' >
            <p class='search-text'>陕旅优品新年好礼</p>
        </div>
    </div>
    <nut-swiper direction="horizontal" :loop="true" :canDragging="false" :paginationVisible="true" :autoPlay="5000" :speed='1000' class='swiper' ref="swipers">
        <div  class="nut-swiper-slide "  v-for='(item,index) in banner' :key='index'>
            <img :src="item.homePageTurnsImg" alt="" class='banner-img' >
        </div>
    </nut-swiper>
    <div class='nav'>
        <div class='nav-list' v-for='(item,index) in scope' :key='index' @click="listName(item.id)" >
            <img :src="item.pictureUrl" alt="" class='nav-icon'>
            <p class='nav-text'>{{item.typeName}}</p>
        </div>
    </div>
    <div class='notic'>
        <img src="../../../static/image/home-notic.png" alt="" class='notic-img' >
        <nut-swiper direction="vertical" :autoPlay="5000" :speed='1000' class='notic-swiper'>
            <div  class="nut-swiper-slide " v-for='(item,index) in notic ' :key='index' >
                {{item}}
            </div>
        </nut-swiper>
    </div>
    <div class='qiang'>
        <div class='time-box'>
            <div class='qiang-tips'>
                <div class='qiang-tips-list'>
                    <img class='qiang-icon' src='../../../static/image/home-qiang-icon.png' />
                    <img class='qiang-titles' src='../../../static/image/home-qiang-title.png'/>
                    <!-- <div class='qiang-time'>
                        <div class='qiang-times'>{{hours}}</div>
                        <div class='qiang-ge'>:</div>
                        <div class='qiang-times'>{{min}}</div>
                        <div class='qiang-ge'>:</div>
                        <div class='qiang-times'>{{seconds}}</div>
                    </div> -->
                </div>
                <div class='qiang-tips-list' bindtap='moreQiang'>
                    <span class='qiang-more-text'>更多抢购</span>
                    <img class='qiang-more' src='../../../static/image/home-qiang-more.png'/>
                </div>
            </div>
            <div class='qiang-box'>
                <div class='qiang-list-box' v-for='(item,index) in timesList' :key='index'   >
                    <img src='../../../static/image/home-qiang-bg.png' class='qiang-list-bg' />
                    <div class='qiang-list' >
                        <img :src='item.picture' class='qiang-img' />
                        <div class='qiang-money'>
                            <span class='qiang-xian'>￥{{item.sellPrice}}</span>
                            <span class='qiang-yuan' v-if='item.marketPrice!=null' >￥{{item.marketPrice}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
    <div class='activeBox' v-for='(item,index) in activeList' :key='index' >
        <img :src="item.img" alt="" class='active-img' >
        <div class='pro-box'>
            <div class='pro-list' v-for='(items,index) in item.productList' :key='index' @click='detailsName(items.productId)'>
                <img :src="items.picture" alt="" class='pro-img' >
                <p class='pro-name'>{{items.title}}</p>
                <p class='pro-price'>
                    <span class='active-money'>￥{{items.sellPrice}}</span>
                    <span class='yuan-money' v-if='items.marketPrice!=null' >￥{{items.marketPrice}}</span>
                </p>
            </div>
        </div>
        <div class='active-more' @click='listMore(item.activityId)'>
            <img src="../../../static/image/home-more-icon.png" alt="" class='active-more-icon' >
            <p class='active-more-text'>更多商品</p>
        </div>
    </div>
</div>
</template>

<script>
    import {dou,banner,scope,notic,qiang,qiangs,dong} from '../../api/api';
    export default {
    //import引入的组件需要注入到对象中才能使用
    components: {},
    data() {
    return {
        banner:[],
        scope:[],
        notic:[],
        timesList:[],
        timesShow:true,
        activeList:[]
    };
    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        // 跳转详情页
        detailsName:function(id){
            this.$router.push('/shopDetails/'+id)
        },
        // 跳转列表页
        listName:function(id){
            this.$router.push('/shopList/'+id)
        },
        // 跳转专题页
        listMore:function(id){
            this.$router.push('/listMore/'+id)
        },
        // 跳转搜索页
        searchName:function(){
            this.$router.push('/search')
        }
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {
        this.$store.dispatch('headTitle','陕旅优品')
        this.$store.dispatch('goBack',false)
        this.$store.dispatch('showBtm',true)
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
        this.loading = this.$toast.loading();
        banner().then(response => {
            this.loading.hide();
            this.banner=response.data.data.SlAdvertisTabList
             this.$refs.swipers.updateEvent()
        });
        scope().then(response => {
             this.loading.hide();
            this.scope=response.data.data.WxProductTypeList
        });
        notic().then(response => {
             this.loading.hide();
            if(response.data.status!=1001){
                this.notic=response.data.data.list
            }
        });
        qiang(1).then(response => {
            
        });
        qiangs(1).then(response => {
             this.loading.hide();
            if(response.data.status==1001){
                this.timesShow=false
            }else{
                this.timesShow=true
               this.timesList=response.data.data.productList 
            }
        });
        dong().then(response => {
             this.loading.hide();
            if(response.data.status!=1001){
                this.activeList=response.data.data
            }
        });
    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
    .home{
        background:#f7f7f7;
        padding-bottom:120px;
        padding-top:90px;
    }
    .search-box{
        width:100%;
        height:100px;
        display:flex;
        align-items:center;
        background:#fff;
    }
    .search{
        width:670px;
        height:70px;
        border-radius:50px;
        background:#f5f6f7;
        display:flex;
        justify-content: flex-start;
        align-items:center;
        margin:0 auto;
    }
    .search-icon{
        display:block;
        width:40px;
        height:40px;
        margin-right:16px;
        margin-left:30px;
    }
    .search-text{
        font-size:12px;
        color:rgb(143,143,143);
    }
    .swiper{
        height:375px;
    }
    .banner-img{
        width:100%;
        height:375px;
    }
    .nav{
        display:flex;
        justify-content:space-between;
        flex-wrap:wrap;
        padding:30px 0;
        background:#fff;
    }
    .nav-list{
        width:180px;
        margin-bottom:30px;
    }
    .nav-icon{
        display:block;
        width:100px;
        height:100px;
        margin:0 auto;
        margin-bottom:10px;
    }
    .nav-text{
        width:100%;
        text-align:center;
        font-size:12px;
        color:rgb(106,106,106);
    }
    .notic{
        width:710px;
        margin: 60px auto;
        display:flex;
    }
    .notic-img{
        display:block;
        width:120px;
        height:28px;
        margin-right:20px;
    }
    .notic-swiper{
        width:590px;
        height:40px;
    }
    .notic-swiper .nut-swiper-slide{
        font-size:14px;
        color:#333;
        font-weight:bold;
        height:40px;
        text-align: left;
    }
/* 限时抢购 */ 
    .qiang{ 
        width:670rpx;
        padding:30rpx 20rpx; 
        background:#fff; 
        border-radius:20rpx;
        position: relative;
        margin:0 auto;
        overflow:hidden;
        height:325rpx;
        margin-bottom:46rpx;
    } 
    .time-bg{
        width:100%;
    }
    .time-box{
        width:670rpx;
        padding:0 20rpx;
        position:absolute;
        left:0;
        top:0;
    }
    .qiang-tips{
        width:100%;
        overflow:hidden;
        height:110rpx;
        display:flex;
        align-items:center;
        justify-content:space-between;
    }
    .qiang-tips-list{
        display:flex;
        align-items:center;
    }
    .qiang-icon{
        width:50rpx;
        height:50rpx;
        display:block;
        margin-right:14rpx;
    }
    .qiang-titles{
        width:146rpx;
        height:34rpx;
        display:block;
        margin-right:16rpx;
    }
    .qiang-more-text{
        font-size:28rpx;
        color:rgb(255,26,48);
        margin-right:20rpx;
    }
    .qiang-more{
        width:32rpx;
        height:32rpx;
        display:block;
    }
    .qiang-time{ 
        display:flex; 
        justify-content:center; 
        align-items:center; 
    } 
    .qiang-times{ 
        width:40rpx; 
        height:40rpx; 
        border-radius:8rpx; 
        text-align:center; 
        line-height:40rpx; 
        font-size:26rpx; 
        color:#fff; 
        background: rgb(255,0,0); 
    } 
    .qiang-ge{ 
        margin:0 10rpx; 
        color:rgb(255,0,0); 
    } 
    .qiang-box{ 
        width:100%; 
        display:flex; 
        flex-wrap:wrap; 
        justify-content:space-around;
    } 
    .qiang-list-box{
        width:163rpx;
        height:254rpx;
        position:relative;
    }
    .qiang-list{ 
        width:163rpx; 
        display:block;
        position:absolute;
        left:0;
        top:0;
    } 
    .qiang-list-bg{
        width:100%;
        height:100%;
        display:block;
    }
    .qiang-img{ 
        width:163rpx; 
        height:163rpx; 
        display:block; 
    } 
    .qiang-name{ 
        width:100%; 
        overflow:hidden; 
        text-overflow: ellipsis; 
        font-size:22rpx; 
        color:#000; 
        margin-top:10rpx; 
        margin-bottom:15rpx; 
        white-space: nowrap; 
        
    } 
    .qiang-money{ 
        display:flex; 
        flex-direction:column;
        justify-content:center;
        height:90rpx;
    } 
    .qiang-xian{ 
        font-size:32rpx; 
        color:rgb(254,237,211);
        font-weight:bold;
        width:100%;
        text-align:center;
    } 
    .qiang-yuan{ 
        font-size:26rpx; 
        color:rgb(245,205,140);
        font-weight:bold;
        width:100%;
        text-align:center;
        text-decoration: line-through; 
    } 
    .end{ 
        width:100%; 
        height:80rpx; 
        text-align:center; 
        line-height:80rpx; 
        font-size:28rpx; 
        color:rgb(193,193,193); 
    }
    .tiHuo{
        width:210rpx;
        height:210rpx;
        position:fixed;
        right:0;
        bottom:60rpx;
        animation:tada 1.5s  linear both infinite;
    }
    .hui-icon{
        width:210rpx;
        height:210rpx;
    }
// 活动模块
    .activeBox{
        background:#fff;
    }
    .active-img{
        width:100%;
        display:block;
    }
    .pro-box{
        width:100%;
        padding:0 18px;
        display:flex;
        flex-wrap:wrap;
    }
    .pro-list{
        width:230px;
        height:350px;
        margin-bottom:10px;
        margin-right:12px;
    }
    .pro-list:nth-child(3n){
        margin-right:0;
    }
    .pro-img{
        width:230px;
        height:230px;
        display:block;
    }
    .pro-name{
        font-size:12px;
        color:#000;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .pro-price{
        width:100%;
        display:flex;
    }
    .active-money{
        font-size:14px;
        color:#f00;
        margin-right:14px;
    }
    .yuan-money{
        font-size:12px;
        color:rgb(159,159,159);
        text-decoration: line-through;
    }
    .active-more{
        width:100%;
        margin:20px 0;
        position: relative;
        padding-bottom:50px;
    }
    .active-more-icon{
        display:block;
        width:198px;
        height:26px;
        margin:0 auto;
    }
    .active-more-text{
        width:100%;
        text-align:center;
        font-size:14px;
        color:#f00;
        position:absolute;
        left:0;
        top:0;
    }
</style>


